"use strict";

var React = React;
var ctx = ctx;
var messages = lski.messages;

var BrowserDetailsBar = React.createClass({
	displayName: "BrowserDetailsBar",

	propTypes: {
		selectedItem: React.PropTypes.object,
		onItemDeleted: React.PropTypes.func.isRequired
	},
	onDeleteClicked: function onDeleteClicked() {
		var _this = this;

		messages.confirm('Are you sure you want to delete this file? \n\nIt could be being used', function (answer) {

			if (!answer) {
				return;
			}

			ctx.deleteItem(_this.props.selectedItem.id).then(function () {
				return _this.props.onItemDeleted(_this.props.selectedItem);
			});
		});
	},
	render: function render() {

		var item = this.props.selectedItem;

		if (!item || !item.isFile) {
			return null;
		}

		return React.createElement(
			"footer",
			{ className: "details" },
			React.createElement(
				"div",
				null,
				React.createElement("img", { className: "delete", src: "content/images/close.png", onClick: this.onDeleteClicked }),
				React.createElement(
					"a",
					{ className: "file-selected", href: item.link, target: "_blank" },
					item.name
				)
			)
		);
	}
});